@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.3/css/bulma.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

section#test-ui-look {
  height: 100vh;

  color: #ccc;

  display: flex;
  justify-content: center;
  align-items: center;
}

section div {
  border: solid 0.5px #ffc;
  padding: 4em;
  width: 80vw;
}

section .badge {
  width: 5em;
  height: 5em;
  margin: 2em;
}

.App {
  text-align: center;
  background-color: hsl(218, 86%, 5%);
  color: #ccc;
  height: 100vh;
  width: 100vw;
}

.button {
  background-color: transparent;
  color: #ccc;
}

#iframe {
  grid-area: overlap;

  width: 100%;
  height: 100%;
  overflow: visible;
}

#question {
  width: 0;
  opacity: 1;
  overflow: hidden;
  grid-area: overlap;
  color: #ccc;
  z-index: 10;
  height: 100%;
  background-color: #cccccc66;
  transition: all 0.6s ease;
}

#question.show {
  opacity: 1;
  width: 100%;
}

#iframe-container {
  position: absolute;
  width: 100%;
  height: 80vh;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr;
  grid-template-areas: "overlap";
}

div.answer-button-wrapper {
  display: flex;
  flex-direction: row;
  grid-gap: 2em;
  gap: 2em;
  justify-content: center;
}

div.answer-button-wrapper button {
  background-color: #cccccc66;
}

button.show-question-button {
  padding: 1.5em;
  border-radius: 0 3px 3px 0;
  background-color: #cccccc66;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: absolute;
  left: 0;
  top: 0;
}

#nav-bar {
  display: flex;
  flex-flow: column;
}

#nav-bar .row {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
}
#nav-bar .row a,
#nav-bar .row button {
  display: inline;
}

